import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import { AdaptivityProvider, AppRoot, Panel, Card } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home'
import GetToken from './panels/GetToken'

import './App.css'

const App = () => {
	const [activePanel, setActivePanel] = useState('new-user');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [modal, setModal] = useState(null)
	const [token, setToken] = useState({access_token: 0})

	const setTheme = async () => {
		await bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
	}

	useEffect(() => {
		

		async function fetchData() {
			await setTheme()
			await setTheme()
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel={activePanel} popout={popout} modal={modal}>
					<Home 
					id="home" 
					fetchedUser={fetchedUser} 
					setModal={setModal} 
					setPopout={setPopout}
					token={token}
					go={go}/>
					<GetToken 
					id='new-user' 
					go={go}
					setToken={setToken}/>
				</View>			
			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;

