import React, {useState} from 'react'

import { Panel, PanelHeader, Placeholder, Button, Separator, Group, Snackbar, Avatar } from '@vkontakte/vkui'
import { Icon56UsersOutline, Icon56MentionOutline, Icon16ErrorCircleOutline } from '@vkontakte/icons'
import bridge from '@vkontakte/vk-bridge'
const GetToken = ({id, setToken, go}) => {
    const [snackbar, setSnackbar] = useState(null)

    const getToken = async () => {
		try {
		const access_token = await bridge.send('VKWebAppGetAuthToken', {
				app_id: 7790406,
				scope: '',
		})
			setToken(access_token)
            go({currentTarget: {dataset: {to: 'home'}}})
		} catch (e) {
			setSnackbar(<Snackbar
                onClose={() => setSnackbar(null)}
                before={<Avatar size={24} style={{ background: 'var(--destructive)' }}>
                    <Icon16ErrorCircleOutline fill="#fff" width={14} height={14} />
                    </Avatar>}
              >
                Ошибка! Необходимо выдать доступ к общей информации профиля, для того чтобы получить данные о ваших ссылках!
              </Snackbar>)
		}
	}

    return <Panel id={id}>
        <PanelHeader>Сокращение cсылок</PanelHeader>

        <Group>
                <Placeholder
                  icon={<Icon56UsersOutline />}
                  action={<Button size="m" onClick={getToken}>Продолжить</Button>}
                >
Приложению необходимо выдать доступ к общей информации вашего аккаунта 
для того чтобы получить информацию о ваших сокращенных ссылках!

                </Placeholder>
              </Group>
              {snackbar}
    </Panel>
}

export default GetToken 