import React, {useEffect, useState} from 'react'

import qr from '@vkontakte/vk-qr';
import bridge from '@vkontakte/vk-bridge'
import { Icon24Dismiss, Icon56MentionOutline } from '@vkontakte/icons';
import { Icon24Poll, Icon16Done} from '@vkontakte/icons';
import { Icon16ErrorCircleOutline } from '@vkontakte/icons';

import { Button, Snackbar, Avatar, Spinner, 
    ModalRoot, ModalPage, ModalPageHeader, PanelHeaderButton,
    Alert, Placeholder } from '@vkontakte/vkui';

import { Icon20DeleteOutline } from '@vkontakte/icons';

import './UserUrl.css'

/* 
<Button size="s" style={{margin: 5}} onClick={() => {
                            bridge.send("VKWebAppCopyText", {text: x.short_url});
                            setSnackbar(
                                <Snackbar
                                  onClose={() => setSnackbar(null)}
                                  before={<Avatar size={24} style={{ background: 'var(--accent)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
                                >
                                  Ссылка скопирована
                                </Snackbar>
                            )
                        }}>Копировать</Button>
                        <Button  size="s" mode="destructive" style={{margin: 5}} 
                        onClick={() => {
                            deleteLink(x)
                        }}>Удалить</Button>
                        
                    <Button mode="tertiary"><p className="links-check-qr" onClick={() => {
                        setModalRoot(x, qr_code, date)
                    }}>Показать</p></Button>
*/

const UserUrl = ({links, setModal, setSnackbar, setPopout, restart, token}) => {
    const [linksGrid, setLinksGrid] = useState(<Spinner size="medium" style={{ margin: '20px 0' }} />)

    const delLink = async (link) => {
        try {
            const photos = await bridge.sendPromise('VKWebAppCallAPIMethod', {
            method: 'utils.deleteFromLastShortened',
            request_id: '324nnefj',
    
            params: {
              v: '5.95',
              key: link.key,
              access_token: token.access_token,
            },
            })

            setSnackbar(
            <Snackbar
                onClose={() => setSnackbar(null)}
                before={<Avatar size={24} style={{ background: 'var(--accent)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
              >
                Ссылка удалена
              </Snackbar>
              )
              restart()
        } catch (e) {
            setSnackbar(<Snackbar
                onClose={() => setSnackbar(null)}
                before={<Avatar size={24} style={{ background: 'var(--destructive)' }}>
                    <Icon16ErrorCircleOutline fill="#fff" width={14} height={14} />
                    </Avatar>}
              >
                  Ошибка! Попробуйте позже!
              </Snackbar>)
        }
    }

    const deleteLink = async (link) => {
        setPopout(<Alert
        actionsLayout="horizontal"
        actions={[
          {
            title: 'Отмена',
            autoclose: true,
            mode: 'cancel',
          },
          {
            title: 'Удалить',
            autoclose: true,
            mode: 'destructive',
            action: () => {
                delLink(link);
            },
          },
        ]}
        onClose={() => setPopout(null)}
      >
        <p>Вы уверены, что хотите удалить ссылку {link.short_url}?</p>
      </Alert>)
    }

    const setModalRoot = (link, svg, date) => {
        setModal(
    <ModalRoot activeModal='select'>
        <ModalPage 
            dynamicContentHeight
            settlingHeight={100}
            id='select' 
            header={
            <ModalPageHeader
            right={
                <PanelHeaderButton onClick={() => {
                    setModal(null)
                }}><Icon24Dismiss/>
                </PanelHeaderButton>}
        >Подробная информация
        </ModalPageHeader>
    }>
        <div className='links-qr-code-max' dangerouslySetInnerHTML={{
            __html: svg
        }}></div>
        <div className='links-qr-code-statistic-all'>
            Короткая ссылка: {link.short_url}</div>
        <div className='links-qr-code-statistic-all-link'>
            Ваша ссылка: {link.url}</div>
        <div className='links-qr-code-statistic-all-date'>Дата создания: {date}</div>
        <div className='links-qr-code-statistic'>
            <Icon24Poll width={14} height={14} style={{marginRight: 5}}/>
            Переходы: {link.views}</div>
        </ModalPage>
      </ModalRoot>
        )
    }

    useEffect(() => {
        console.log(links);
       
        let items = []
        links.items.map(x => {
            const qr_code = qr.createQR(x.short_url, {
                qrSize: 256,
                isShowLogo: true,
                isShowBackground: true,
                backgroundColor: '#ffffff'
            });
            let data = new Date(x.timestamp * 1000)
            let date = `${data.getDate() < 10 ?
                '0' + data.getDate() :
                 data.getDate()}.${data.getMonth() < 10 ?
                    "0" + (data.getMonth() + 1) : 
                    data.getMonth() + 1 }.${data.getFullYear()} ${data.getHours() < 10 ?
                        "0" + data.getHours() :
                         data.getHours()}:${data.getMinutes() < 10 ?
                            "0" + data.getMinutes() :
                             data.getMinutes()}`
            items.push(
                <div key={Math.round(Math.random() * 1000000)} className='links-grid-items'>
                    <div className='links-qr-code' dangerouslySetInnerHTML={{
                        __html: qr_code
                    }}
                    onClick={() => {
                        setModalRoot(x, qr_code, date)
                    }}
                    ></div>
                    <div className='links-body'>
                        <div className='links-short-url'>{x.short_url}</div>
                        <div className='links-url'>{x.url}</div>
                        <div className='links-date'>{date}</div>
                    </div>
                    <div className='links-body-infp'>
                        <Icon20DeleteOutline 
                        width={15} 
                        height={15} 
                        style={{marginBottom: 20, marginLeft: 15}}
                        onClick={() => {
                            deleteLink(x)
                        }}/>
                        <div className="links-body-vues">
                            <Icon24Poll 
                            width={10} 
                            height={10}
                            style={{marginLeft: 10}}
                            /> {x.views}
                            </div>
                    </div>
                </div>
            )
        })
        if (links.items.length < 1) {
            items = (<Placeholder
            icon={<Icon56MentionOutline />}
          >
Здесь будет информация о ваших сокращенных ссылках.
          </Placeholder>)
        }
        setLinksGrid(items)
    }, [links])

    return linksGrid
}

export default UserUrl